
.Main-container{
    display: flex;
    flex-direction: row;
    background-color: rgb(253, 209, 187)
}

.Sidebar{

    width: 20%;
    /* height: 100%; */
    position: sticky;
}

.sidecss{
    width: 20%;
}

/* .homeli{
    color: black
} */

.homeli, .vocalesli, .profileli, .abcli, .numli, .globli{
    text-transform: uppercase;
}

.Main-Sections-Container{
    display: flex;
    align-items: center;
    flex-direction: column;
    /* border: 1px rgb(167, 167, 167) solid; */
    width: 80%;
}

.section-vocals{
    display: flex;
    margin-top: 2%;
    border: rgb(46, 211, 0) solid;
    border-radius: 40px 40px 40px 40px;
    background-color: rgb(46, 211, 0);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    height: 60px;
    width: 60%;
    font-size:x-large;
    color: white;
    font-family:Arial, Helvetica, sans-serif;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.section-letters{
    display: flex;
    border: rgb(0, 199, 206) solid;
    border-radius: 40px 40px 40px 40px;
    background-color: rgb(0, 199, 206);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    height: 60px;
    width: 60%;
    font-size:x-large;
    color: white;
    font-family:Arial, Helvetica, sans-serif;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.section-number{
    display: flex;
    border: rgb(190, 128, 226) solid;
    border-radius: 40px 40px 40px 40px;
    background-color: rgb(190, 128, 226);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    height: 60px;
    width: 60%;
    font-size:x-large;
    color: white;
    font-family:Arial, Helvetica, sans-serif;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.section-global{
    display: flex;
    border: rgb(255, 16, 135) solid;
    border-radius: 40px 40px 40px 40px;
    background-color: rgb(255, 16, 135);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    height: 60px;
    width: 60%;
    font-size:x-large;
    color: white;
    font-family:Arial, Helvetica, sans-serif;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.section-number, .section-vocals, .section-letters, .section-global {
    letter-spacing: 2px;
}

.vocal-path{
    display: flex;
    column-gap: 30%;
}

.btnlist{
    display: flex;
    flex-direction: column;
    margin-top: 10%;
    /* column-gap: 20%; */
    /* padding: 50px; */
}

.btn, .btn1, .btn2, .btn3, .btn4, .btn5, .btn6 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 20%;
    border: 1px solid rgb(46, 211, 0);
    background-color:rgb(46, 211, 0); ;
    height: 80px;
    width: 80px;
    border-radius: 50%;
}

.section-title {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
}

#star, #puzzle, #aslhand, #dragon, #Aicon #launchbox{
    height: 80%;
    width: auto;
}

#party, #Memory{
    height: 60%;
    width: auto;
}

#numerosicon, #mujer, #casa, #tablero, #gamess, #abcicon {
    height: 33px;
    width: auto;
}


.btnA, .btnA1, .btnA2, .btnA3, .btnA4, .btnA5 {
    display: flex;
    margin: 20%;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 0;
    border: 1px solid rgb(0, 199, 206);
    background-color: rgb(0, 199, 206);
    height: 80px;
    width: 80px;
    border-radius: 50%;
}

.btn01, .btn11, .btn22, .btn33, .btn44, .btn55 {
    display: flex;
    margin: 20%;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 0;
    border: 1px solid rgb(190, 128, 226);
    background-color:rgb(190, 128, 226);
    height: 80px;
    width: 80px;
    border-radius: 50%;
}

.btnG0, .btnG1, .btnG2, .btnG3, .btnG4, .btnG5 {
    display: flex;
    margin: 20%;
    justify-content: center;
    align-content: center;
    align-items: center;
