@media (max-width: 965px) and (min-width:670px){
    .homeli, .vocalesli, .profileli, .abcli, .numli, .globli{
        display: none;
}
.sidecss{
    width: 100px;
}
.L-text{
    font-size: large;
}
}

@media (max-width: 669px) and (min-width:375px){
    .homeli, .vocalesli, .profileli, .abcli, .numli, .globli{
        display: none;
}
.sidecss{
    width: 100px;
}
.L-text{
    font-size:medium;
}

.btn, .btn1, .btn2, .btn3, .btn4, .btn5, .btn6, .btnA, .btnA1, .btnA2, .btnA3, .btnA4, .btnA5 {
