  .nav-elements {
    position: absolute;
    opacity: 90%;
    right: 0;
    top: 70px;
    background-color: #ff881a;
    width: 0px;
    height: calc(100vh - 70px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .nav-elements.active {
    width: 50%;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .nav-elements ul li {
    margin-right: 10px;
    margin-top: 22px;
  }
